.contacts {
    height: 100vh;
    background-color: #1f1f20;
    display: flex;
    justify-content: center;
}
.container {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 0;
}
.formWrapper {
    display: flex;
    flex-direction: column;
    width: 500px;
}

.btnSubmit {
    width: 150px;
    height: 50px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    border: none;
}
.messageArea {
    min-height: 160px;
}
@media screen and (max-width: 626px) {
    .formWrapper {
        width: 100%;
    }
}